import React from 'react';
import {
    Button,
    Spinner

} from '@vkontakte/vkui';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon16Chevron from '@vkontakte/icons/dist/16/chevron';
import Icon16Done from '@vkontakte/icons/dist/16/done';
class SubscribeBlockMobile extends React.Component{

    constructor(props){
        super(props);
        this.state= {
            animeId: props.anime.anime_id,
            animeName: props.anime.anime_name,
            animeNameRus: props.anime.anime_name_rus,
            shikiId: props.anime.shiki_id,
            subStatus: props.anime.sub_status,
            dubStatus: props.anime.dub_status,
            shikiResp:undefined,
            opened:false,
            showed:props.anime.showed,
        };
        this.click = this.click.bind(this);
        this.clickButton = this.clickButton.bind(this);
    }

    click(e){
        this.setState({opened:!this.state.opened})
    }

    clickButton(e,type){
        console.log(e)
        if( (type===0 && (this.state.subStatus!==-2 && this.state.subStatus!==-1)) || (type===1 && (this.state.dubStatus!==-2 && this.state.dubStatus!==-1))){
            switch(type){
                case 0:
                    this.setState({subStatus:-2});
                    break;
                case 1:
                    this.setState({dubStatus:-2});
                    break;
            }

        }


    }

    getButtonStyle(status){
        switch (status) {
            case -1:
                return "secondary";
            case 0:
                return "primary";
            case 1:
                return "outline";
            case -2:
                return "secondary";

        }
    }
    getBeforeSymbol(status){
        switch(status){
            case 1:
                return <Icon16Done/>;
            case -2:
                return <Spinner size={"small"} />
        }
    }

    render(){
        if(this.state.showed){
            return (

                <div style={{"display":"flex","fontSize":"13px","borderTop":"0px", "borderBottom":"1px", "borderRight":"0px", "borderLeft":"0px", "borderStyle":"solid", "borderColor":"var(--button_bot_shadow)", "marginBottom":"5px", "paddingTop":"5px", "paddingBottom":"5px","backgroundColor":"var(--background_content)", "padding":"12px", "color":"var(--accent)",}} onClick={this.click}>
                    <div style={{"display":"flex","float":"left", "alignItems":"center","justifyContent":"center","minWidth":"300"}}>
                        <div style={{}}>
                            {this.state.opened ? <Icon16Dropdown fill="var(--accent)"/> : <Icon16Chevron fill="var(--accent)"/>}
                        </div>
                    </div>

                    <div style={{"display":"flex","float":"left", "marginLeft":"7px"}}>{this.state.animeName}<br/>{this.state.animeNameRus}</div>
                    <div style={{"display":"flex", "width":"auto"}}>
                        <Button stretched level={this.getButtonStyle(this.state.subStatus)} before={this.getBeforeSymbol(this.state.subStatus)} onClick={(e)=>this.clickButton(e,0)} style={{"minWidth":"120px"}}>Субтитры</Button>
                        <Button stretched level={this.getButtonStyle(this.state.dubStatus)} before={this.getBeforeSymbol(this.state.dubStatus)} onClick={(e)=>this.clickButton(e,1)} style={{"minWidth":"120px"}}>Озвучка</Button>
                    </div>
                </div>

            );
        }
    }
}

export default SubscribeBlockMobile;